import service from "@/utils/http";

export const get_git = (data: any) => {
  const refactor_data = {
    page_num: 1,
    page_size: 10,
    query: "",
    search_type: "",
  };
  refactor_data.page_num = data.page_num;
  refactor_data.page_size = data.page_size;
  refactor_data.search_type = data.search_type;
  if (data.query == "") {
    refactor_data.query = "null";
  } else {
    refactor_data.query = data.query;
  }
  return service.get(
    `/git/${refactor_data.search_type}/${refactor_data.page_num}/${refactor_data.page_size}/${refactor_data.query}`
  );
};

export const post_git = (data: any) => {
  return service.post("/git/allmonitor", data);
};

export const manual_monitor = (monitor_id: string) => {
  return service.post("/git/singlemonitor", {
    monitor_id:monitor_id
  });
}


export const delete_git = (monitor_id_list: any) => {
  return service.delete('/git', {data:monitor_id_list})
};

export const get_detail = (data: any) => {
  const refactor_data = {page_num: 1, page_size: 10, query: '', search_type: '', current_monitor_id: '', detail_type: ''}
  refactor_data.page_num = data.page_num
  refactor_data.page_size = data.page_size
  refactor_data.search_type = data.search_type
  refactor_data.current_monitor_id = data.current_monitor_id
  refactor_data.detail_type = data.detail_type
  if(data.query == ''){
      refactor_data.query = 'null'
  }else{
      refactor_data.query = data.query
  }
  return service.get(`/gitdetail/${refactor_data.detail_type}/${refactor_data.search_type}/${refactor_data.current_monitor_id}/${refactor_data.page_num}/${refactor_data.page_size}/${refactor_data.query}`)
}

export const flush_record = (detail_type, monitor_id) => {
  return service.post(`/gitdetail/${detail_type}`, {
    monitor_id: monitor_id
  })
}

export const get_overview = (current_monitor_id: string) => {
  return service.get(`/gitdetail/overview/${current_monitor_id}`)
}