import { reactive } from 'vue'

interface task_data_form_rules {
    task_name: {
        message: string;
        required: boolean;
        trigger: string;
    }[];
    task_target: {
        message: string;
        required: boolean;
        trigger: string;
    }[];
}


export const task_data_form_rules = reactive<task_data_form_rules>({
    task_name: [{
        message: '任务名称不能为空...',
        required: true,
        trigger: 'blur' // 元素失去焦点的时候触发
    }],
    
    task_target: [{
        message: '目标仓库不能为空...',
        required: true,
        trigger: 'blur'
    }]
})
